import NotificationTime from "../NotificationTime";

function NotificationStatus({ notification }) {
  switch (notification.type) {
    case 'NEWCONTRACT':
      return (
        <>
          <span> gi:</span> <span className="time"> <NotificationTime key={`notification_time_${notification.id}`} notify={notification} /></span>
        </>
      );
    case 'SIGN':
      return (
        <>
          <span> k:</span> <span className="time"><NotificationTime key={`notification_time_${notification.id}`} notify={notification} /></span>
        </>
      );
    case 'COMMENT':
      return (
        <>
          <span> phn hi:</span> <span className="time"><NotificationTime key={`notification_time_${notification.id}`} notify={notification} /></span>
        </>
      );
    case 'EXPIRE':
      return (
        <>
          <span>Ht hn vo:</span> <span className="time"><NotificationTime key={`notification_time_${notification.id}`} notify={notification} /></span>
        </>
      );

    default:
      return <></>;
  }
}
export default NotificationStatus;