import React, { useEffect, useRef, useState } from 'react';
import $ from 'jquery';
import { IconToastBug, IconToastBugSystem, IconToastCheckUser, IconToastCode, IconToastExpired, IconToastNoResources, IconToastNotFile, IconToastSuccess, IconToastSuccessContract, IconToastNotReadFile, IconToastSuccessInfor, IconToastUSBToken, IconToastUSBTokenLogin, IconToastSaveFile } from '@Components/Icon';
const ToastIcon = ({ type }) => {
    switch (type) {
        case 'ERROR':
            return <IconToastBug />;
        case 'ERROR_SIGN':
            return <IconToastBug />;
        case 'CODE_EXPIRED':
            return <IconToastExpired />;
        case 'OTP_FAILED':
            return <IconToastExpired />;
        case 'ACCOUNT_NOTFOUND':
            return <IconToastNoResources />;
        case 'NO_RESOURCE':
            return <IconToastNoResources />;
        case 'NOT_FILE':
            return <IconToastNotFile />;
        case 'NOT_READ_FILE':
            return <IconToastNotReadFile />;
        case 'BUG_SAVE_FILE':
            return <IconToastSaveFile />;
        case 'ERROR_SYSTEM':
            return <IconToastBugSystem />;
        case 'USER_EXIST':
            return <IconToastCheckUser />;
        case 'ERROR_AUTHOR':
            return <IconToastCode />;
        case 'SUCCESS_CONTACT':
            return <IconToastSuccessInfor />;
        case 'SUCCESS_INFOR':
            return <IconToastSuccessInfor />;
        case 'SUCCESS_SIGN_CONTRACT':
            return <IconToastSuccessContract />;
        case 'ATTACH_USBTOKEN_SIGN':
            return <IconToastUSBToken />;
        case 'ATTACH_USBTOKEN_SIGNUP':
            return <IconToastUSBTokenLogin />;
        case 'SUCCESS_CANCEL_CONTRACT':
            return <IconToastSuccess />;
        default:
            return <IconToastSuccess />;
    }
};
const ToastContent = ({ type, message = 'Li h thng', contractName = '', isCreateContract }) => {
    switch (type) {
        case 'ERROR':
            return <p class="text-center">{message}</p>;
        case 'ERROR_SIGN':
            return <><p>Trong qu trnh k kt <b>{contractName}</b>  pht sinh li.</p>
                <p>Qu khch vui lng th li!</p></>
        case 'CODE_EXPIRED':
            return <p class="text-center">M xc thc khng hp l hoc  ht hn.</p>;
        case 'OTP_FAILED':
            return <p class="text-center">M xc thc khng hp l hoc  ht hn.</p>;
        case 'ACCOUNT_NOTFOUND':
            return <p class="text-center">Ti khon cha ng k.</p>;
        case 'NO_RESOURCE':
            return <p class="text-center">Ti nguyn khng tn ti.</p>;
        case 'NOT_FILE':
            return <p class="text-center">Khng tm thy Hp ng.</p>;
        case 'NOT_READ_FILE':
            return <p class="text-center">H thng khng c c tp tin Hp ng.</p>;
        case 'BUG_SAVE_FILE':
            return <p class="text-center">Li trong qu trnh lu Hp ng.</p>;
        case 'ERROR_SYSTEM':
            return <p class="text-center">H thng pht sinh li.</p>;
        case 'USER_EXIST':
            return <p class="text-center">Ti khon  tn ti!</p>;
        case 'ERROR_AUTHOR':
            return <>
                <p>Qu trnh {isCreateContract ? 'to' : 'k'} <b>{contractName}</b> b sai xc thc.
                </p>
                <p>Li {message}</p>
                <p>Vui lng kim tra thng tin USB Token.</p>
            </>
        case 'SUCCESS_CONTACT':
            return <p class="text-center">Gi lin h thnh cng!</p>;
        case 'SUCCESS_INFOR':
            return <p class="text-center">Cp nht thng tin thnh cng.</p>;
        case 'SUCCESS_SIGN_CONTRACT':
            return <p class="text-center" >K kt thnh cng <b>{contractName}</b>.</p>;
        case 'ATTACH_USBTOKEN_SIGN':
            return <p class="text-center">Vui lng <b>cm USB Token</b>  tip tc k s!</p>;
        case 'ATTACH_USBTOKEN_SIGNUP':
            return <p class="text-center">Vui lng <b>cm USB Token</b>  hon tt ng k ti khon <b><a href="https://mcontract.vn/" target="_blank">MContract</a></b>!</p>;
        case 'SUCCESS_CANCEL_CONTRACT':
            return <p class="text-center">Hy Hp ng thnh cng!</p>;
        default:
            return <p class="text-center">{message}</p>;
    }
}


const Toast = ({ id, contractName, type = 'ERROR', message, color, onDestroy, isCreateContract = false }) => {
    const setClassColor = (color) => {
        switch (color) {
            case 'red':
                return 'bg_site_bug';
            case 'green':
                return 'bg_site_contract';
            case 'purple':
                return 'bg_site_code';
            default:
                return 'bg_site';
        }
    }

    useEffect(() => {
        setTimeout(function () {
            $('.toast_site').fadeOut(350);
            setTimeout(() => {
                onDestroy(id);
            }, 350);
        }, 3000);
    }, []);

    return <>
        <div className="contain-toast">
            <div className="toast_site shadow">
                <div className={`toast_icon ${setClassColor(color)}`}>
                    <ToastIcon type={type} />
                </div>
                <div className="p-3">
                    <ToastContent contractName={contractName} type={type} message={message} isCreateContract={isCreateContract} />
                </div>
                <div className={`toast_footer ${setClassColor(color)}`} >
                    <div className="footer_arrow"></div>
                </div>
            </div>
        </div>
    </>
}
export default Toast
